.input__wrapper {
  display: flex;
  width: 100%;
  border-bottom: 1px solid $graySecondary;
  transition: all 0.3s;
  cursor: pointer;
  font-size: 20px;
  &:hover,
  &:focus {
    border-bottom-color: $white; }
  .input {
    height: 100%;
    min-height: 34px;
    width: 100%;
    font-weight: 200;
    outline: 0px;
    background: transparent;
    border: none;
    color: $white;
    @include placeholder-color($graySecondary); } }

