@import "mixin";
@import "variables";
@import "utilities";
@import "input";

@import "bootstrap24.css";
@import "base.css";

@include font-face($headName, 'fonts/VW_Head/VWHeadWeb-Bold', bold);
@include font-face($headName, 'fonts/VW_Head/VWHeadWeb-Regular', normal);
@include font-face($headName, 'fonts/VW_Head/VWHeadWeb-Light', 200);
@include font-face($textName, 'fonts/VW_Text/VWTextWeb-Bold', bold);
@include font-face($textName, 'fonts/VW_Text/VWTextWeb-Regular', normal);
@include font-face($textName, 'fonts/VW_Text/VWTextWeb-RegularItalic', normal, italic);
@include font-face($textName, 'fonts/VW_Text/VWTextWeb-Light', 200);


.login__stage {
  position: relative;
  height: 100vh;
  &-container {
    background: $blueDark;
    color: $white; }
  &-image-container {
    height: 100%;
    width: 100%;
    img {
      object-fit: cover;
      object-position: 50% 50%;
      height: 100%;
      width: 100%;
      filter: brightness(80%); } } }


.modal-video {
    background-color: rgba(0, 0, 0, 0.9); }

.InfiniteCarouselScrollTrack {
  .InfiniteCarouselSlide {
    vertical-align: top;
    &:first-child {
      margin-left: 0 !important; }
    &:last-child {
      margin-right: 0 !important; } } }
.InfiniteCarouselDots {
  .InfiniteCarouselDot {
    outline: none;
    .InfiniteCarouselDotIcon {
      background: #DEE3E7;
      &.InfiniteCarouselDotActiveIcon {
        background: #000000; } } } }
