.car-canvas {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

.car-grid {
  display: grid;

  @media only screen and (min-width: 768px) {
    grid-template-columns: auto auto auto;
    grid-gap: 40px; }

  .card-grid-item {
    text-decoration: none;
    color: #000000;
    img {
      width: 100%;
      max-width: 480px;
      display: block;
      margin: 0 auto; }
    &-description {
      font-family: 'vw head', serif;
      font-size: var(--texttypes-headline300-fontsize);
      line-height: var(--texttypes-headline300-lineheight);
      letter-spacing: var(--texttypes-headline300-letterspacing); }
    p {
      display: inline; }
    span {
      margin-left: 5px;
      font-weight: bold; } } }


.car-description__wrapper {
  position: absolute;
  left: -41vw;
  top: -35vh;
  color: #ffffff;
  font-family: 'vw head', serif;

  .car-description__title {
    font-size: var(--texttypes-headline300-fontsize);
    line-height: var(--texttypes-headline300-lineheight);
    letter-spacing: var(--texttypes-headline300-letterspacing);
    font-weight: bold;
    margin: 0; }


  .car-description__description {
    font-size: var(--texttypes-headline300-fontsize);
    line-height: var(--texttypes-headline300-lineheight);
    letter-spacing: var(--texttypes-headline300-letterspacing);
    font-weight: 200;
    margin: 10px 0 0; } }

.car-360__wrapper {
  position: absolute;
  right: -35vw;
  top: -35vh;
  color: #ffffff; }

